@keyframes bounce {
    from {
        transform: translateY(0px)
    }

    to {
        transform: translateY(-20px)
    }
}

@keyframes equalize {
    0% {
        transform: translateY(0);
    }

    5% {
        transform: translateY(1px);
    }

    10% {
        transform: translateY(2px);
    }

    15% {
        transform: translateY(3px);
    }

    20% {
        transform: translateY(4px);
    }

    25% {
        transform: translateY(5px);
    }

    30% {
        transform: translateY(4px);
    }

    35% {
        transform: translateY(3px);
    }

    40% {
        transform: translateY(2px);
    }

    45% {
        transform: translateY(1px);
    }

    50% {
        transform: translateY(0px);
    }

    55% {
        transform: translateY(-1px);
    }

    60% {
        transform: translateY(-2px);
    }

    65% {
        transform: translateY(-3px);
    }

    70% {
        transform: translateY(-4px);
    }

    75% {
        transform: translateY(-5px);
    }

    80% {
        transform: translateY(-4px);
    }

    85% {
        transform: translateY(-3px);
    }

    90% {
        transform: translateY(-2px);
    }

    95% {
        transform: translateY(-1px);
    }

    100% {
        transform: translateY(0);
    }
}